import React from "react"
import { useIntl } from "gatsby-plugin-intl"
import _ from "lodash"
import Layout from "../components/Layout"
import Header from "../components/Header"
import TextHeader from "../components/TextHeader"
import json from "../intl/pl.json"
import Wrapper from "../components/Wrapper"

const IndexPage = () => {
    const intl = useIntl()
    console.log(intl)

    return (
        <Layout isHomepage>
            <Header page="1">
                <TextHeader
                    link
                    title={intl.formatMessage({ id: "home.header.title" })}
                    desc={intl.formatMessage({ id: "home.header.desc" })}
                />
            </Header>
            {_.map(json.home.content, (e, i) => {
                console.log(e, i, json.home.content.length)
                return (
                    <Wrapper
                        isBlack={i % 2 === 0}
                        videoSrc={`home-${i}`}
                        imgSrc={"dsadasd"}
                        title={intl.formatMessage({
                            id: `home.content.${i}.title`,
                        })}
                        desc={intl.formatMessage({
                            id: `home.content.${i}.desc`,
                        })}
                        z={i + 1}
                        isSecond={i === 0}
                        isLast={i === json.home.content.length - 1}
                    />
                )
            })}
        </Layout>
    )
}
export default IndexPage
